import * as jwtDecode from 'jwt-decode';
import moment from 'moment';
import EventBus from "../event-bus";

export const extractFromRoute = (query, filters, sorts) => {
    let path = "";
    let title_starts = "";
    for (const key in query) {
        if (Object.prototype.hasOwnProperty.call(query, key)) {
            if (query[key] === null)
                continue;
            if (Array.isArray(query[key])) {
                path.concat(key).concat('=').concat(query[key][0]);
                if (key === "title_starts")
                    title_starts = query[key][0]
            } else {
                path += key + "=" + query[key];
                if (key === "title_starts")
                    title_starts = query[key]
            }
            path += "&&"

        }
    }

    let filter = null;
    let sort = { id: null, dir: 1 };

    // eslint-disable-next-line no-unused-vars
    if (Object.prototype.hasOwnProperty.call(query, 'filter')) {
        filter = filters.find(_filter => _filter.id === query['filter']).id;
    }

    // eslint-disable-next-line no-unused-vars
    for (let [index, element] of sorts.entries()) {
        if (Object.prototype.hasOwnProperty.call(query, element.id)) {
            sort.id = element.id;
            sort.dir = parseInt(query[element.id]);
        }
    }
    path = path.slice(0, path.length - 2);

    return { path, title_starts, filter, sort }
};

export const buildSearchPath = (action, data, title_starts, filter, sort) => {
    if (action === 'filter') {
        if (!filter || filter !== data.id) {
            filter = data.id;
        } else
            filter = null;
    } else if (action === 'sort') {
        if (sort.id === data.id) {
            sort.dir *= -1
        } else
            sort.id = data.id
    }
    let query = { title_starts: title_starts };
    if (sort.id)
        query[sort.id] = sort.dir.toString();
    if (filter)
        query.filter = filter;

    return { query, filter, sort }
};

export const authHeader = () => {

    let token = localStorage.getItem('token');
    // const payload = jwtDecode(token);

    if (token) {
        return { 'Authorization': 'Bearer ' + token };
    } else {
        return {};
    }
};


export const displayDate = (createdDate) => {
    let date = new Date(Date.parse(createdDate));

    return ('0' + date.getDate()).slice(-2) + '-' + ('0' + (date.getMonth() + 1)).slice(-2) + '-'
        + ('0' + date.getFullYear()).slice(-2)
        + ' o ' + ('0' + date.getHours()).slice(-2) + ':' + ('0' + date.getMinutes()).slice(-2);
};

export const isLoggedIn = () => {
    if (localStorage.getItem('token') === null) {
        EventBus.$emit('logged', 'out');
        return false;
    }

    if (moment().isAfter(getExpiration())) {
        EventBus.$emit('logged', 'out');
        return false;
    } else {
        return true;
    }
};


const getExpiration = () => {
    const token = localStorage.getItem('token');
    const payload = jwtDecode(token);
    return moment.unix(payload.exp);
};

export const getUserId = () => {
    if (localStorage.getItem('token') !== null) {
        const token = localStorage.getItem('token');
        const payload = jwtDecode(token);
        return payload.id;
    }
    return null;
};

export const getUsername = () => {
    if (localStorage.getItem('token') !== null) {
        const token = localStorage.getItem('token');
        const payload = jwtDecode(token);
        return payload.username[0].concat(payload.username[1]).toUpperCase();
    }
    return "";
};

